/* Font and Background */
.customer-details-page {
    font-family: 'Roboto', sans-serif;
    background-color: #f8f8f8;
    padding: 3rem;
  }
  
  /* Page Title */
  .page-title {
    text-align: center;
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 3rem;
  }
  
  /* Page Content */
  .page-content {
    display: flex;
    justify-content: space-between;
    gap: 3rem;
  }
  
  .customer-details-form,
  .customer-details-display {
    background-color: white;
    padding: 3rem;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    flex-basis: 48%;
  }
  
  /* Form Elements */
  .form-group {
    margin-bottom: 2rem;
  }
  
  label {
    font-weight: 500;
    display: block;
    margin-bottom: 0.75rem;
  }
  
  input {
    width: 100%;
    padding: 1rem;
    border: 1px solid #e0e0e0;
    border-radius: 6px;
    font-size: 1.1rem;
    transition: border-color 0.3s ease;
  }
  
  input:focus {
    outline: none;
    border-color: #007bff;
  }
  
  .save-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .save-button:hover {
    background-color: #0056b3;
  }
  
  /* Customer Details Display */
  .section-title {
    font-size: 2.25rem;
    font-weight: 600;
    margin-bottom: 2rem;
  }
  
  .details-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
  }
  
  .details-item {
    border-bottom: 1px solid #e0e0e0;
    padding-bottom: 1.5rem;
  }
  
  .item-title {
    font-weight: 500;
    margin-bottom: 0.75rem;
  }
  
  .item-value {
    font-size: 1.2rem;
  }

  